import "./styles.css";
import Headers from "./Headers";
import SearchBox from "./SearchBox";
import data1 from "./data.json";
import TraverseFoodData from "./TraverseFoodData";
import React, { useState, useEffect, useRef } from "react";

export default function App() {
  const FoodData = data1;
  const [ingredentList, setIngredentList] = useState(FoodData.hits);
  const [search, setsearch] = useState("");
  const [loading, setloading] = useState(true);
  const inputRef = useRef(null);

  useEffect(() => {
    Search("eggs");
  }, []);

  const Search = (query) => {
    console.log(`query ${query}`);
    let url = `https://api.edamam.com/search?q=${query}&app_id=2e1a4f11&app_key=21c8696a056b4cc871562f9314641dca`;
    fetch(url)
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        console.log(data);
        setIngredentList(data.hits);
        setloading(false);
      })
      .catch((error) => console.log("error", error));
  };

  const clickHandler = () => {
    let query = inputRef.current.value;
    Search(query);
  };

  return (
    <div className="App">
      <Headers />

      <input type="text" ref={inputRef} placeholder="Search for Recipe" />
      <button onClick={clickHandler}>Search</button>
      {loading && <h1>Loading .....</h1>}
      <div className="wrapper">
        {ingredentList.map((item) => {
          return (
            <div className="Ingredents">
              <span>
                <p>{item.recipe.label}</p>
              </span>
              <span>
                <img src={item.recipe.image} />
              </span>
              <div className="steps">
                <ol>
                  {item.recipe.ingredientLines.map((steps) => {
                    return <li>{steps}</li>;
                  })}
                </ol>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
